<template>
  <div>
    <BorderedTitle><slot></slot></BorderedTitle>
    <ul class="recommendNewMusic" :class="{mb:currentMusic}">
      <SongItem
        v-for="(item, index) in newSongs"
        :key="item.id"
        :item="item"
        @update:music="$emit('update:music', $event)"
        :currentMusic="$attrs.currentMusic"
        :paused="$attrs.paused"
        @update:playlist="updatePlaylist(index)"
      />
      <!-- @update:playlist="$emit('update:playlist', { newSongs, index })" -->
    </ul>
  </div>
</template>

<script>
import BorderedTitle from "@/components/BorderedTitle.vue";
import SongItem from "@/components/SongItem.vue";
export default {
  components: {
    BorderedTitle,
    SongItem,
  },
  props: {
    newSongs: {
      type: Array,
    },
    currentMusic: {
      type: Object,
    },
  },
  methods: {
    updatePlaylist(index) {
      var obj = {
        songListItem: this.newSongs,
        index: index,
      };
      this.$emit("update:playlist", obj);
    },
  },
};
</script>

<style>
.recommendNewMusic {
  box-sizing: border-box;
}
.mb {
  margin-bottom: 100px;
}
</style>