<template>
  <div class="singerList">
    <!-- 头部 -->
    <header>
      <template v-if="isShow">
        <div class="loading-box-1">
          <img src="../../public/img/loading.svg" class="loading" />
        </div>
      </template>
      <img :src="`${singer.picUrl}?imageView=1&type=webp&thumbnail=1126x0`" v-else />
      <div class="loading-box-title">{{singer.name}}</div>
    </header>

    <!-- 歌手介绍部分 -->
    <template v-if="singer.briefDesc">
      <h2>歌手简介</h2>
      <div class="singerMsg">
        <p :class="{ toFull: !isShowMsg }">{{ singer.briefDesc }}</p>
        <div class="full" @click="isShowMsg = !isShowMsg">
          {{ isShowMsg ? "收起" : "完整歌手介绍" }}
          <i class="arrow" :class="{ 'arrow-up': isShowMsg }"></i>
        </div>
      </div>
    </template>

    <!-- 歌曲部分 -->
    <h2>热门单曲</h2>
    <section>
      <ul>
        <template v-if="isShow">
          <div class="loading-box-2">
            <img src="../../public/img/loading.svg" class="loading" />
          </div>
        </template>
        <template v-else>
          <HotSongItem
            v-for="(item, index) in songs"
            :key="index"
            :index="index"
            :item="item"
            @update:music="$emit('update:music', $event)"
            :currentMusic="$attrs.currentMusic"
            :paused="$attrs.paused"
            @update:playlist="updatePlaylist(index)"
          />
        </template>
      </ul>
    </section>
  </div>
</template>

<script>
import HotSongItem from "@/components/HotSongItem.vue";
export default {
  components: {
    HotSongItem,
  },
  data() {
    return {
      singer: {},
      songs: [],
      loading: 0,
      isShow: true,
      isShowMsg: false,
    };
  },
  watch: {
    "$route.query.id": function (n) {
      if (n) {
        console.log("id更新了", this.$route.query.id);
        this.getSinger();
        this.getSongs();
      }
    },
    loading(n) {
      if (n >= 2) {
        this.isShow = false;
        console.log(this.isShow);
      }
    },
  },
  created() {
    console.log("created", this.$route.query.id);
    this.getSinger();
    this.getSongs();
  },
  methods: {
    getSinger() {
      this.isShow = true;
      this.axios
        .get("/artists?id=" + this.$route.query.id)
        .then((res) => {
          // console.log(res.data.artist);
          this.singer = res.data.artist;
        })
        .finally(() => {
          this.loading++;
        });
    },
    getSongs() {
      this.isShow = true;
      this.axios
        .get("/artists?id=" + this.$route.query.id)
        .then((res) => {
          if (!res) return;
          console.log(res);
          var arr = [];
          for (let i = 0; i < 5; i++) {
            arr.push({
              name: res.data.hotSongs[i].name,
              picUrl: res.data.hotSongs[i].al.picUrl,
              id: res.data.hotSongs[i].id,
              cd: res.data.hotSongs[i].cd, //判断是否为SQ歌曲
              song: [...res.data.hotSongs[i].ar],
            });
          }
          this.songs = arr;
        })
        .finally(() => {
          this.loading++;
        });
    },
    updatePlaylist(index) {
      var obj = {
        songListItem: this.songs,
        index: index,
      };
      this.$emit("update:playlist", obj);
    },
  },
};
</script>

<style scoped lang="less">
.singerList {
  h2 {
    font-size: 17px;
    margin-top: 8px;
    padding: 15px;
    font-weight: 700;
  }
  img.loading {
    width: 100px;
    height: 100px;
    margin: 0;
  }
  header {
    position: relative;
    .loading-box-1 {
      min-height: 293px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    position: relative;
    &::after {
      position: absolute;
      content: "";
      background: rgba(0, 0, 0, 0.2);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    img {
      vertical-align: middle;
      width: 100%;
    }
    .loading-box-title {
      position: absolute;
      bottom: 120px;
      left: 16px;
      color: #fff;
      z-index: 2;
      font-size: 20px;
      line-height: 20px;
    }
  }
  .singerMsg {
    padding: 0 16px 18px;
    box-sizing: border-box;
    p {
      color: #666;
      font-size: 14px;
      &.toFull {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
      }
    }
    .full {
      margin-top: 25px;
      text-align: center;
      font-size: 14px;
      color: #666;
      .arrow {
        background: url(data:image/png;base64,);
        background-repeat: no-repeat;
        width: 9px;
        height: 9px;
        display: inline-block;
        background-size: contain;
      }
      .arrow-up {
        transform: rotate(180deg);
      }
    }
  }
  section {
    margin-bottom: 100px;
    .loading-box-2 {
      display: flex;
      justify-content: center;
      padding-top: 50px;
    }
  }
}
</style>