<template>
  <ul v-if="singerId && singer">
    <li
      class="singer"
      @click="push(s.id)"
      v-for="(s, index) in singer"
      :key="index"
    >
      <div class="singer-img">
        <img :src="`${s.picUrl}?imageView=1&type=webp&thumbnail=151x0`" />
      </div>
      <h4>歌手：{{ s.name }}</h4>
      <i></i>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["singerId"],
  data() {
    return {
      singer: [],
    };
  },
  created() {
    this.searchSinger(this.singerId);
  },
  watch: {
    singerId(n) {
      this.searchSinger(n);
    },
  },
  methods: {
    searchSinger(id) {
      this.singer = [];
      id.forEach((item) => {
        this.axios.get("/artists?id=" + item).then((res) => {
          if (!res) {
            return;
          }
          this.singer.push(res.data.artist);
        });
      });
      console.log(this.singer);
    },
    push(id) {
      this.$router.push({
        path: "/singerList",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="less">
.singer {
  width: calc(100% - 10px);
  height: 66px;
  margin-left: 10px;
  padding: 8px 10px 8px 0px;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  .singer-img {
    width: 50px;
    height: 50px;
    margin-right: 15px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  h4 {
    flex: 1;
    height: 30px;
    font-size: 17px;
    line-height: 30px;
    color: #333;
  }
  i {
    margin-right: 8px;
    margin-left: 10px;
    width: 8px;
    height: 13px;
    background: url("../../public/img/youjiantou.svg");
  }
}
</style>