<template>
  <div class="playlist">
    <section>
      <div class="mask" :style="`backgroundImage:url('${itemPicUrl}')`"></div>
      <div class="detail">
        <div class="detail_pic">
          <img :src="itemPicUrl" />
          <span class="detail_songlist">歌单</span>
          <span class="detail_num">{{ playCount | formatPlayCount }}</span>
        </div>
        <div class="detail_font">
          <h2>{{ title }}</h2>
          <div>
            <img :src="`${userImg}?param=40y40`" />
            <span>{{ userNickName }}</span>
          </div>
        </div>
      </div>
    </section>

    <div class="pylst_list">
      <div class="pylst_list_header">歌曲列表</div>
      <img v-if="loading" class="loading" src="../../public/img/loading.svg" />
      <ul v-else>
        <HotSongItem
          v-for="(item, index) in songList"
          :key="index"
          :index="index"
          :item="item"
          @update:music="$emit('update:music', $event)"
          :currentMusic="$attrs.currentMusic"
          :paused="$attrs.paused"
          @update:playlist="updatePlaylist(index)"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import HotSongItem from "@/components/HotSongItem.vue";
export default {
  components: {
    HotSongItem,
  },
  data() {
    return {
      listId: null,
      itemPicUrl: "",
      playCount: "",
      userId: null,
      userImg: null,
      userNickName: null,
      title: "",
      songList: [],
      loading: false,
    };
  },
  watch: {
    // 监听id的变换
    "$route.query.id": function (n) {
      if (n) {
        console.log("id更新了");
        this.getData();
      }
    },
    // 监听歌单用户id变动
    userId() {
      this.getUserData();
    },
  },
  // 组件初始化渲染
  created() {
    console.log("创建");
    this.getData();
  },
  methods: {
    // 查询歌曲数据
    getData() {
      // 获取与item相关的数据
      this.listId = this.$route.query.id;
      this.itemPicUrl = this.$route.query.itemPicUrl;
      this.playCount = this.$route.query.playCount;
      // 数据请求
      this.axios
        .get("/playlist/detail?id=" + this.$route.query.id)
        .then((res) => {
          // console.log(res);
          this.userId = res.data.playlist.userId;
          this.title = res.data.playlist.name;
          var str = res.data.playlist.trackIds
            .slice(0, 30)
            .map((item) => {
              return item.id;
            })
            .join();
          this.axios.get("/song/detail?ids=" + str).then((res) => {
            console.log(res.data.songs);
            // this.songList = res.data.songs;

            // 修改获取到的数组数据
            var arr = [];
            for (let i = 0; i < res.data.songs.length; i++) {
              arr.push({
                name: res.data.songs[i].name,
                picUrl: res.data.songs[i].al.picUrl,
                id: res.data.songs[i].id,
                cd: res.data.songs[i].cd, //判断是否为SQ歌曲
                song: [...res.data.songs[i].ar],
              });
              // arr[i].song = [...res.data.songs[i].ar];
            }
            this.songList = [...arr];
          });
        });
    },
    // 获取歌单用户的数据
    getUserData() {
      if (!this.userId) {
        return;
      }
      this.loading = true;
      this.axios
        .get("/user/detail?uid=" + this.userId)
        .then((res) => {
          // console.log("123", res);
          this.userImg = res.data.profile.avatarUrl;
          this.userNickName = res.data.profile.nickname;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    updatePlaylist(index) {
      var obj = {
        songListItem: this.songList,
        index: index,
      };
      this.$emit("update:playlist", obj);
    },
  },
  filters: {
    formatPlayCount(num) {
      return (num / 10000).toFixed(1) + "万";
    },
  },
};
</script>

<style lang="less">
.loading {
  display: block;
  width: 100px;
  height: 100px;
  margin: 50px auto;
}
.playlist {
  section {
    position: relative;
    padding: 30px 10px 30px 15px;
    overflow: hidden;
    box-sizing: border-box;
    .mask {
      z-index: -1;
      width: 100%;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      filter: blur(20px);
      transform: scale(1.5);
      &::after {
        content: "";
        display: block;
        z-index: 1;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background: rgba(0, 0, 0, 0.25);
      }
    }
    .detail {
      color: #fff;
      z-index: 3;
      display: flex;
      width: 100%;
      height: 126px;
      .detail_pic {
        position: relative;
        width: 126px;
        height: 126px;
        .detail_songlist {
          position: absolute;
          top: 15px;
          left: 0;
          background: rgba(217, 48, 48, 0.8);
          width: 40px;
          height: 17px;
          font-size: 12px;
          display: inline-block;
          text-align: center;
          border-top-right-radius: 7px;
          border-bottom-right-radius: 7px;
        }
        .detail_num {
          position: absolute;
          top: 0;
          right: 0;
          color: #fff;
          font-size: 13px;
          &::before {
            content: "";
            display: inline-block;
            height: 12px;
            width: 13px;
            vertical-align: middle;
            margin-right: 2px;
            background: no-repeat
              url("data:image/svg+xml;base64,");
            background-size: auto 100%;
            background-position: left center;
          }
        }
      }
      .detail_font {
        flex: 1;
        margin-left: 16px;
        height: 126px;
        h2 {
          font-size: 17px;
        }
        div {
          margin-top: 20px;
          img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
          }
          span {
            display: inline-block;
            max-width: 45%;
            height: 30px;
            line-height: 30px;
            margin-left: 5px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
      }
    }
  }
  .pylst_list {
    .pylst_list_header {
      height: 23px;
      line-height: 23px;
      padding: 0 10px;
      font-size: 12px;
      color: #666;
      background-color: #eeeff0;
    }
    ul {
      img {
        .loading;
      }
    }
  }
}
</style>