<template>
  <div class="footer">
    <div class="process">
      <span>{{ minCur }}</span>
      <input
        type="range"
        min="0"
        :max="duration"
        :step="1"
        :value="currentTime"
        @input="changeCurTime"
        ref="dur"
      />
      <span>{{ minDur }}</span>
    </div>
    <div class="handler">
      <ul>
        <li
          @click="toggleModule(playModule)"
          :class="{
            roundModule: playModule == 0,
            randomModule: playModule == 1,
            singleModule: playModule == 2,
          }"
        ></li>
        <li @click="$emit('prev-song')"></li>
        <li :class="{ stop: !paused }" @click="$emit('update:paused')"></li>
        <li @click="$emit('next-song')"></li>
        <li @click="isShowMylist = !isShowMylist"></li>
      </ul>
    </div>
    <template v-if="isShowMylist">
      <div class="playFullSongList" ref="listBox">
        <ol
          ref="list"
          @touchstart="touchstart"
          @touchmove="touchmove"
          @touchend="touchend"
        >
          <PlayFullSongList
            v-for="(item, index) in playlist"
            :key="index"
            :index="index"
            :item="item"
            @change-song="$emit('change-song', index);isShowMylist = !isShowMylist"
          />
        </ol>
        <span @click="isShowMylist = false">关闭</span>
      </div>
    </template>
  </div>
</template>

<script>
import PlayFullSongList from "@/components/PlayFullSongList.vue";
export default {
  components: {
    PlayFullSongList,
  },
  data() {
    return {
      isShowMylist: false,
      startY: 0,
      curY: 0,
      resY: 0,
      isTouchStart: false,
    };
  },
  props: [
    "currentTime",
    "duration",
    "playModule",
    "paused",
    "playlist",
    "closeList",
  ],
  computed: {
    // 计算正在播放的歌曲的分钟制时间(已播放的时间)
    minCur: function () {
      let allSec = this.currentTime;
      let min = parseInt(allSec / 60);
      min = min < 10 ? "0" + min : min;
      let second = parseInt(allSec % 60);
      second = second < 10 ? "0" + second : second;
      let str = min + ":" + second;
      return str;
    },
    // 计算正在播放的歌曲的分钟制总时间
    minDur: function () {
      let allSec = this.duration;
      let min = parseInt(allSec / 60);
      min = min < 10 ? "0" + min : min;
      let second = parseInt(allSec % 60);
      second = second < 10 ? "0" + second : second;
      let str = min + ":" + second;
      return str;
    },
    // 计算歌单列表的总高度
    listHeight() {
      return this.playlist.length * 40;
    },
    // 计算滑动的总高度
    maxSlide() {
      let num = this.$refs.listBox.offsetHeight - this.listHeight - 50;
      if (num > 0) {
        num = 0;
      }
      return num;
      // -50 是为了预留高度
    },
  },
  watch: {
    // 改变当前的进度条的背景颜色占比
    currentTime(n) {
      var per = parseInt((n / this.duration) * 100);
      this.$refs.dur.style.backgroundSize = per + "% 100%";
    },
    closeList() {
      this.isShowMylist = false;
    },
  },
  methods: {
    // 获取当前的进度条的播放时间
    changeCurTime(e) {
      this.$emit("update:currentTime", e.target.value);
    },
    // 切换切歌模式
    toggleModule(m) {
      m++;
      m = m > 2 ? 0 : m;
      // console.log("下一模式", m);
      this.$emit("update:module", m);
    },
    // 修改歌单位置
    touchstart(e) {
      this.isTouchStart = true;
      this.startY = e.touches[0].pageY;
      // console.log(this.startY);
    },
    touchmove(e) {
      if (this.isTouchStart) {
        var moveY = e.touches[0].pageY;
        this.resY = moveY - this.startY + this.curY;

        // test
        // console.log('s',this.startY);
        // console.log('m',moveY);
        // console.log('r',this.resY);
        // console.log('c',this.curY);

        if (this.resY > 0) {
          this.resY = 0;
        }
        if (this.resY < this.maxSlide) {
          //.0 等于 最小滑动的距离
          this.resY = this.maxSlide;
        }
        // 修改当前的ol的top
        this.$refs.list.style.top = this.resY + "px";
      }
    },
    touchend() {
      this.curY = this.resY;
      this.isTouchStart = false;
    },
  },
};
</script>

<style scoped lang="less">
.footer {
  position: absolute;
  bottom: 0;
  height: 100px;
  width: 100vw;
  .process {
    // 进度条的样式重置
    display: flex;
    align-items: center;
    span {
      width: 50px;
      font-size: 6px;
      text-align: center;
      color: rgba(255, 255, 255, 0.6);
    }
    input {
      flex: 1;
      -webkit-appearance: none;
      margin: 0 auto;
      // width: 70vw;
      height: 2px;
      background: -webkit-linear-gradient(white, white) no-repeat,
        rgba(255, 255, 255, 0.3);
      // background-size: 75% 100%;
    }
    // 进度条的拖动的按钮样式重置
    input[type="range"]::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 10px;
      width: 10px;
      background: white;
      border-radius: 50%;
    }
  }
  .handler {
    ul {
      display: flex;
      margin-top: 10px;
      li {
        flex: 1;
        height: 60px;
        background-size: 100%;
        background: no-repeat;
        background-position: center center;
        &:nth-of-type(1) {
          background-size: 25px 25px;
          background-image: url("../../public/img/round.png");
          &.randomModule {
            background-image: url("../../public/img/randomRound.png");
          }
          &.singleModule {
            background-image: url("../../public/img/singleRound.png");
          }
        }
        &:nth-of-type(2) {
          background-size: 45px 45px;
          background-image: url("../../public/img/prevSong.png");
        }
        &:nth-of-type(3) {
          background-size: 65%;
          background-image: url("../../public/img/start2.png");
          &.stop {
            background-image: url("../../public/img/stop2.png");
          }
        }
        &:nth-of-type(4) {
          background-size: 45px 45px;
          background-image: url("../../public/img/nextSong.png");
        }
        &:nth-of-type(5) {
          background-size: 25px 25px;
          background-image: url("../../public/img/list.png");
        }
      }
    }
  }
  .playFullSongList {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 80vh;
    background: rgb(29, 29, 31);
    overflow: hidden;
    ol {
      position: absolute;
      top: 0;
      width: 100vw;
      max-height: 90%;
      // overflow: hidden;
      box-sizing: border-box;
    }
    span {
      position: fixed;
      bottom: 0;
      display: block;
      width: 100vw;
      color: white;
      text-align: center;
      padding: 10px 0;
      font-size: 13px;
      background: rgb(29, 29, 31);
    }
  }
}
</style>