<template>
  <div class="search">
    <!-- 搜索框 -->
    <div class="searchingBox">
      <div class="inputBox">
        <i class="find"></i>
        <!-- 回车搜索-->
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model.trim="inputValue"
          @input="searchSuggestWords"
          @keyup.enter="searchSong"
          @click="searching = true"
        />
        <div class="delete">
          <!-- 点击删除按钮 -->
          <i
            @click="
              inputValue = '';
              searching = true;
            "
          ></i>
        </div>
      </div>
    </div>

    <template v-if="searching">
      <!-- 热门框 -->
      <div class="hotTips" v-if="!inputValue">
        <h3>热门搜索</h3>
        <keep-alive>
          <ul class="hotWords">
            <img
              v-if="loading"
              class="loading"
              src="../../public/img/loading.svg"
            />
            <HotWordsItem
              v-for="(item, index) in hotWords"
              :key="index"
              :item="item"
              @update:inputWord="inputValue = $event"
              @search-me="searchSong"
            />
          </ul>
        </keep-alive>
        <!-- 搜索历史框 -->
        <div class="local" v-if="localData">
          <ul>
            <LocalItem
              v-for="(item, index) in localData"
              :key="index"
              :index="index"
              :item="item"
              @update:inputValue="inputValue = $event"
              @search-me="searchSong"
            />
          </ul>
        </div>
      </div>

      <!-- 搜索提示框 -->
      <div class="searchTips" v-else>
        <span class="searchTipsTitle" @click="searchSong"
          >搜索<span v-if="inputValue">"{{ inputValue }}"</span></span
        >
        <ul>
          <img
            v-if="sugLoading"
            class="loading"
            src="../../public/img/loading.svg"
          />
          <SearchWordsItem
            v-for="(item, index) in suggestWordsArr"
            :item="item"
            :key="index"
            @update:inputValue="inputValue = $event"
            @search-me="searchSong"
          />
        </ul>
      </div>
    </template>

    <template v-else>
      <!-- 搜索结果列表 -->
      <div :class="{ mb: $attrs.currentMusic }">
        <img
          v-if="listLoading"
          class="loading"
          src="../../public/img/loading.svg"
        />
        <SearchResult
          v-else
          :newSongs="songs"
          @update:music="$emit('update:music', $event)"
          :currentMusic="$attrs.currentMusic"
          :paused="$attrs.paused"
          @update:playlist="$emit('update:playlist', $event)"
          :singerId="singerId"
        />
        <!-- :singer="singer" -->
      </div>
    </template>
  </div>
</template>

<script>
import HotWordsItem from "@/components/HotWordsItem.vue";
import SearchWordsItem from "@/components/SearchWordsItem.vue";
import SearchResult from "@/components/SearchResult.vue";
import LocalItem from "@/components/LocalItem.vue";
export default {
  components: {
    HotWordsItem,
    SearchWordsItem,
    SearchResult,
    LocalItem,
  },
  data() {
    return {
      songs: [],
      inputValue: "",
      hotWords: [],
      searchSuggestWordsTimer: null, //建议词延时器
      suggestWordsArr: [],
      searching: true, //判断是否显示歌曲列表
      // 用来控制loading标签效果
      loading: true, //热词搜索状态
      sugLoading: false, //建议词搜索状态
      listLoading: true, //歌词列表搜索状态

      // 某个歌手id数据
      singerId: [],
      // 本地存储的内容
      localData: [],
    };
  },
  created() {
    // 获取 本地存储内容
    this.getLocal();
    // 热词搜索
    this.loading = true;
    this.axios
      .get("/search/hot")
      .then((res) => {
        if (!res) return;
        let arr = [];
        // console.log(res.data.result.hots);
        res.data.result.hots.forEach((item) => {
          arr.push(item.first);
        });
        this.hotWords = [...arr];
      })
      .finally(() => {
        this.loading = false;
      });
  },
  methods: {
    // 搜索建议词
    searchSuggestWords() {
      if (this.searchSuggestWordsTimer) {
        clearTimeout(this.searchSuggestWordsTimer);
      }

      this.sugLoading = true;
      this.suggestWordsArr = [];
      this.searchSuggestWordsTimer = setTimeout(() => {
        if (
          this.inputValue == null ||
          this.inputValue == undefined ||
          this.inputValue == ""
        )
          return; //限制为空不存在的内容进行访问

        this.axios
          .get("/search/suggest?keywords=" + this.inputValue)
          .then((res) => {
            console.log("词", res);
            if (!res.data.result) {
              return;
            }
            let suggestWordsArr = [];
            res.data.result.order.forEach((item) => {
              res.data.result[item].forEach((item) => {
                suggestWordsArr.push(item.name);
              });
            });
            // console.log(suggestWordsArr);
            this.suggestWordsArr = suggestWordsArr;
          })
          .finally(() => {
            this.sugLoading = false;
          });
        clearTimeout(this.searchSuggestWordsTimer);
      }, 1000);
    },
    // 一个专门用于调用axios的方法
    searchSong() {
      this.songs = [];
      this.searching = false;
      let songs = [];
      songs;
      this.listLoading = true;
      if (
        this.inputValue == null ||
        this.inputValue == undefined ||
        this.inputValue == ""
      ) {
        return;
      }

      this.local(); //触发本地存储方法
      this.axios
        .get("/search?keywords=" + this.inputValue)
        .then((res) => {
          if (!res.data.result) {
            return;
          }
          // 获取歌手id
          // console.log(res.data.result.songs[0].artists);
          // 重置id数组
          this.singerId = [];
          for (let i = 0; i < res.data.result.songs[0].artists.length; i++) {
            this.singerId.push(res.data.result.songs[0].artists[i].id);
          }

          // this.singerId = res.data.result.songs[0].artists[0].id;
          res.data.result.songs.forEach((items) => {
            let obj = {};
            for (let key in items) {
              key;
              obj.name = items.name;
              obj.picUrl = items.artists[0].img1v1Url;
              obj.id = items.id;
              obj.song = { ...items };
              obj.song.exclusive = items.fee == 0 ? true : false;
            }
            // console.log(obj);
            this.songs.push(obj);
          });
        })
        .finally(() => {
          this.listLoading = false;
        });
      this.getLocal();
    },
    // 上传数据到本地存储
    local() {
      let stroage = [];
      if (localStorage.getItem("searchWords")) {
        stroage = localStorage.getItem("searchWords").split(","); //获取指定key本地存储的值
      }
      stroage.push(this.inputValue);
      for (let i = 0; i < stroage.length; i++) {
        for (let j = i + 1; j < stroage.length; j++) {
          if (stroage[i] === stroage[j] && i != j) {
            stroage.splice(j, 1);
            j--;
          }
        }
      }
      // console.log(stroage);
      stroage = stroage.toString();
      localStorage.setItem("searchWords", stroage); //将value存储到key字段
    },
    // 获取本地存储，渲染数据
    getLocal() {
      let stroage = localStorage.getItem("searchWords");
      if (!stroage) {
        this.localData = null;
        return;
      }
      stroage = stroage.split(","); //获取指定key本地存储的值
      // console.log('获取',stroage);
      this.localData = stroage;
    },
    //删除本地存储
    removeLocal(index) {
      let stroage = localStorage.getItem("searchWords").split(",");
      stroage.splice(index, 1);
      // console.log("删除后", stroage);
      if (stroage.length == 0) {
        localStorage.removeItem("searchWords");
      } else {
        stroage = stroage.toString();
        localStorage.setItem("searchWords", stroage);
      } 
      this.getLocal();
    },
    changLocalPosition(index) {
      let stroage = localStorage.getItem("searchWords").split(",");
      let ele = stroage.splice(index,1);
      stroage.unshift(ele);
      localStorage.setItem("searchWords", stroage);
      this.getLocal();
    },
  },
  activated() {
    this.searching = true;
    this.songs = [];
    this.inputValue = "";
  },
};
</script>

