<template>
  <li class="playlistitem" @click="navigateToPlayList(item.id)">
    <div class="thumb">
      <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=370x0`" alt="" />
      <span class="play-count">{{ item.playCount | formatPlayCount }}
      </span>
    </div>
    <h6>{{ item.name }}</h6>
  </li>
</template>

<script>
export default {
  name: "PlayListItem",
  props: {
    item: {
      type: Object,
    },
  },
  filters: {
    formatPlayCount(num) {
      return (num / 10000).toFixed(1) + "万";
    },
  },
  methods: {
    navigateToPlayList(id) {
      // console.log(id);
      this.$router.push({
        path:'/playList',
        query:{
          id,
          itemPicUrl:this.item.picUrl,
          playCount:this.item.playCount,
        }
      })

      
    },
  },
};
</script>

<style lang="less">
.playlistitem {
  flex: 1 1 calc(100vw / 3 - 10px);
  box-sizing: border-box;
  .thumb {
    position: relative;
    img {
      min-height: 33vw;
    }
  }
  &:nth-of-type(3n + 2) {
    margin: 0 5px;
  }
  .play-count {
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    font-size: 13px;
    &::before {
      content: "";
      display: inline-block;
      height: 12px;
      width: 13px;
      vertical-align: middle;
      margin-right: 2px;
      background: no-repeat
        url("data:image/svg+xml;base64,");
      background-size: auto 100%;
      background-position: left center;
    }
  }
  h6 {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 5px;
    margin-bottom: 10px;
    font-size: 12px;
  }
}
</style>