<template>
  <div class="searchResult">
    <h3>最佳匹配</h3>
    <Singer 
    :singerId="this.$attrs.singerId"
    />
    <!-- :singer="this.$attrs.singer" -->
    <SongItem
      v-for="(item, index) in newSongs"
      :key="item.id"
      :item="item"
      @update:music="$emit('update:music', $event)"
      :currentMusic="$attrs.currentMusic"
      :paused="$attrs.paused"
      @update:playlist="updatePlaylist(index)"
    />
  </div>
</template>

<script>
import Singer from "@/components/Singer.vue";
import SongItem from "@/components/SongItem.vue";
export default {
  components: {
    Singer,
    SongItem,
  },
  props: {
    newSongs: {
      type: Array,
    },
    // singer: {
    //   type:Object,
    // },
    // singerSongs: {
    //   type:Array,
    // },
  },
  methods: {
    updatePlaylist(index) {
      var obj = {
        songListItem: this.newSongs,
        index: index,
      };
      this.$emit("update:playlist", obj);
    },
  },
};
</script>

<style scoped lang="less">
.searchResult {
  h3 {
    font-size: 12px;
    line-height: 12px;
    color: #666;
  }
}
</style>