<template>
  <div
    class="lyric"
    @click="
      $emit('update:isShowLyric');
      $emit('close-list');
    "
  >
    <ul ref="list" id="list">
      <li v-for="(item, index) in lyric" :key="index">{{ item.text }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["currentWords", "currentTime", "audio", "isShowLyric"],
  data() {
    return {
      evenTop: 208,
    };
  },
  computed: {
    lyric() {
      if (!this.currentWords) {
        return [];
      }
      var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      var arr = this.currentWords
        .split("\n")
        .filter((e) => e)
        .map((str) => {
          var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
          var timeArr = time.split(":");
          time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
          var text = str.replace(patt, "");
          return { time, text };
        });
      return arr;
    },
  },
  created() {
    console.log("created");
    let _this = this;
    this.audio.ontimeupdate = function () {
      _this.updateWords();
    };
  },
  mounted() {
    this.updateWords();
  },
  updated() {
    console.log("update");
    let _this = this;
    this.audio.ontimeupdate = function () {
      _this.updateWords();
    };
  },
  methods: {
    updateWords() {
      if (this.lyric[0].time != 0) {
        this.lyric.unshift({ time: 0, text: "" });
      }
      // 通过判断是否存在this.$refs.list再进行歌词的操作，避免报错
      if (this.$refs.list) {
        let index = this.lyric.findIndex((element) => {
          return element.time > this.currentTime;
        });
        // 如果index接收的返回值为-1，则立刻停止切换歌词
        if (index === -1) {
          return;
        }

        // 获取所有的li
        let lis = this.$refs.list.querySelectorAll("li");
        // 获取当前的li高度
        let liHeight = lis[index - 1].offsetHeight;
        // 这里的208是 div.lyric的高度
        this.$refs.list.style.marginTop = -liHeight * (index - 1) + 208 + "px";
        this.evenTop = -liHeight * (index - 1) + 208;
        // 取消所有li标签的颜色
        for (let j = 0; j < lis.length; j++) {
          lis[j].style.color = "rgb(255,255,255)";
        }
        // 给当前正在播的歌词添加颜色
        lis[index - 1].style.color = "rgb(30,204,148)";
      }
      // else {
      //   console.log("do not have list");
      //   console.log(document.querySelector("#list"));
      //   // document.querySelector("#list").style.top = this.evenTop + "px";
      // }
    },
  },
};
</script>

<style scoped lang="less">
.lyric {
  margin-top: 20px;
  width: 100vw;
  height: 65vh;
  overflow: hidden;
  ul {
    margin-top: 0;
    width: 100vw;
    li {
      width: 100vw;
      min-height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 14px;
    }
  }
}
</style>