<template>
  <div class="cover" @click="$emit('update:isShowLyric');$emit('close-list')">
    <div class="img-box">
      <section :class="{needleAb:paused}"></section>
      <img :src="`${currentMusic.picUrl}?imageView&thumbnail=360y360&quality=75&tostatic=0`" :class="{ stopTurn: paused }" />
    </div>
  </div>
</template>

<script>
export default {
  props: ["currentMusic", "paused"],
};
</script>

<style scoped lang="less">
.cover {
  margin-top: 20px;
  width: 100vw;
  height: 422px;
  // background-color: red;
  padding-top: 70px;
  box-sizing: border-box;
  .img-box {
    margin: 0 auto;
    width: 247px;
    height: 247px;
    background: url("../../public/img/disc.png") no-repeat;
    background-size: 100%;
    border-radius: 50%;
    position: relative;
    section{
      position: absolute;
      top: -66px;
      left: 112px;
      content: "";
      display: block;
      background: url("../../public/img/needle-ab.png") no-repeat;
      background-size: 100%;
      width: 72px;
      height: 118px;
      transform-origin: left top;
      transform:rotate(0deg);
      transition: all 2s;
    }
    .needleAb{
      transform:rotate(-30deg);
    }
    img {
      width: 150px;
      height: 150px;
      border-radius: 50%;
      position: absolute;
      top: calc(50% - 75px);
      left: calc(50% - 75px);
      animation: turn 10s linear infinite;
      &.stopTurn {
        animation-play-state: paused;
      }
    }
  }
  @keyframes turn {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
}
</style>