<template >
  <div class="play" @mousewheel.prevent>
    <!-- @mousewheel.prevent 禁止鼠标滚轮滑动 -->
    <audio
      :src="
        'https://music.163.com/song/media/outer/url?id=' +
        currentMusic.id +
        '.mp3'
      "
      autoplay
      ref="audio"
    ></audio>

    <!-- 自定义播放器 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div class="play-bar" v-show="isShowBar">
        <!-- :src="`${currentMusic.picUrl}?imageView=1&type=webp&thumbnail=369x0`" -->
        <img
          :src="`${currentMusic.picUrl}?imageView=1&type=webp&thumbnail=360x0`"
          @click="isShowBar = false"
          :class="{ playingStop: paused }"
        />
        <div class="font-box">
          <div>
            <span v-text="currentMusic.name"></span>
            <span
              v-for="(artist, index) in currentMusic.song.artists"
              :key="index"
              class="font-box-fontStyle"
            >
              <template v-if="!index"> - </template>
              <template v-if="index"> / </template>{{ artist.name }}
            </span>
          </div>
        </div>
        <div class="switch-box" @click.stop="toggleMusicState">
          <canvas ref="canvas" width="50" height="50"></canvas>
          <span :class="{ controlIcon: paused }"></span>
        </div>
      </div>
    </transition>

    <!-- 弹窗播放器 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <div class="play-full" v-show="!isShowBar" @touchmove.prevent>
        <img
          :src="`${currentMusic.picUrl}?imageView&thumbnail=50y50&quality=15&tostatic=0`"
        />
        <div class="mask">
          <PlayFullHeader
            @update-isshowbar="isShowBar = true"
            :currentMusic="currentMusic"
            @close-list="closeList++"
          />
          <template>
            <PlayFullCover
              :currentMusic="currentMusic"
              v-if="!isShowLyric"
              @update:isShowLyric="isShowLyric = !isShowLyric"
              :paused="paused"
              @close-list="closeList++"
            />
            <PlayFullLyric
              v-else
              @update:isShowLyric="isShowLyric = !isShowLyric"
              :currentWords="currentWords"
              :currentTime="currentTime"
              :audio="$refs.audio"
              :isShowLyric="isShowLyric"
              @close-list="closeList++"
            />
          </template>
          <PlayFullFooter
            v-bind:duration="duration"
            v-bind:currentTime="currentTime"
            @update:currentTime="$refs.audio.currentTime = $event"
            @next-song="nextPlay"
            @prev-song="prevPlay"
            :playModule="playModule"
            @update:module="playModule = $event"
            :paused="paused"
            @update:paused="toggleMusicState"
            @change-song="changeSong"
            :playlist="playlist"
            :closeList="closeList"
          />
          <!-- @next-song是切换到下一首 -->
          <!-- @prev-song是切换到上一首 -->
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import PlayFullHeader from "@/components/PlayFullHeader.vue";
import PlayFullCover from "@/components/PlayFullCover.vue";
import PlayFullLyric from "@/components/PlayFullLyric.vue";
import PlayFullFooter from "@/components/PlayFullFooter.vue";
export default {
  components: {
    PlayFullHeader,
    PlayFullCover,
    PlayFullLyric,
    PlayFullFooter,
  },
  props: ["currentMusic", "playlist", "currentIndex", "currentWords"],
  data() {
    return {
      paused: null,
      isShowBar: true,
      isShowLyric: false,
      duration: 0,
      currentTime: 0,
      playModule: 0, //0:顺序播放,1:随机播放,2:单曲循环
      closeList: 0,
    };
  },
  mounted() {
    var audio = this.$refs.audio;
    audio.addEventListener("pause", () => {
      // 获取audio的播放状态
      this.paused = true;
    });
    audio.addEventListener("play", () => {
      // 获取audio的播放状态
      this.paused = false;
    });
    audio.addEventListener("durationchange", () => {
      // 获取audio的总时长
      this.duration = audio.duration;
    });

    audio.addEventListener("timeupdate", () => {
      // 获取audio的当前播放时长
      this.currentTime = audio.currentTime;
      this.drawcircle(this.currentTime, this.duration);
    });
    // 监听当前的音乐播放结束
    audio.addEventListener("ended", () => {
      if (this.playModule == 2) {
        //如果当前的模式是单曲循环，那么只需要让播放时间重置，和调用toggleMusicState方法让audio重新进入播放状态
        this.currentTime = 0;
        this.toggleMusicState();
      } else {
        this.nextPlay();
      }
    });
  },
  watch: {
    // 监听paused的值，变动了就动态的往父级传
    paused: function (n) {
      this.$emit("update:paused", n);
    },
  },
  methods: {
    // 点击下方播放器右侧开关按钮的圆绘制
    drawcircle: function (c, d) {
      let canvas = this.$refs.canvas;
      let ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, 50, 50);
      ctx.beginPath();
      ctx.strokeStyle = "rgba(0,0,0,0.2)";
      ctx.arc(25, 25, 15, 0, Math.PI * 2);
      ctx.stroke();
      ctx.closePath();
      ctx.beginPath();
      ctx.strokeStyle = "rgba(0,0,0,1)";
      ctx.arc(25, 25, 15, Math.PI * -0.5, Math.PI * ((c / d) * 2 - 0.5), false);
      ctx.stroke();
      ctx.closePath();
    },
    // 点击下方播放器右侧按钮暂停和播放音乐
    // 通过当前的paused来控制音乐播放与暂停
    toggleMusicState() {
      let audio = this.$refs.audio;
      if (this.paused) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    // 一个方法的设计，传一个指定的下标到此方法中，根据改下表获取指定的歌曲
    // 操作下一首歌曲的方法
    nextPlay(myindex) {
      // myindex是playFull里面的歌单传过来的歌曲下标
      myindex = myindex + 1;
      if (myindex) {
        myindex = myindex - 1;
        this.$emit("update:music", {
          item: this.playlist[myindex],
          myindex,
        });
      } else {
        let index = this.calculateNextSongIndex();
        this.$emit("update:music", {
          item: this.playlist[index],
          index,
        });
      }
    },
    // 计算下一首歌的下标
    calculateNextSongIndex() {
      let nextIndex = this.currentIndex;
      if (this.playModule == 0 || this.playModule == 2) {
        // 顺序播放和单曲循环切歌的时候都可以换到下一首
        ++nextIndex;
        nextIndex = nextIndex > this.playlist.length - 1 ? 0 : nextIndex;
      } else if (this.playModule == 1) {
        let randomIndex;
        do {
          // 随机播放
          randomIndex = Math.floor(Math.random() * this.playlist.length);
        } while (randomIndex === nextIndex);

        // 赋值给nextindex
        nextIndex = randomIndex;
      }
      // 返回
      return nextIndex;
    },
    // 操作上一首歌曲的方法
    prevPlay() {
      let index = this.calculatePrevSongIndex();
      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
    // 计算上一首歌的下标
    calculatePrevSongIndex() {
      let prevIndex = this.currentIndex;
      --prevIndex;
      prevIndex = prevIndex < 0 ? this.playlist.length - 1 : prevIndex;
      return prevIndex;
    },
    // playFull歌单切换歌曲
    changeSong(num) {
      console.log(num);
      this.nextPlay(num);
    },
  },
};
</script>

<style scoped lang='less'>
.play {
  z-index: 5;
  width: 100%;
  position: fixed;
  bottom: 0;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15);
  .play-bar {
    background: rgb(247, 234, 234);
    height: 50px;
    display: flex;
    padding-left: 10px;
    img {
      width: 42px;
      height: 42px;
      margin: -5px 7px;
      border-radius: 50%;
      animation: picPlaying 6s linear infinite;
      &.playingStop {
        animation-play-state: paused;
      }
    }
    .font-box {
      flex: 1;
      line-height: 50px;
      padding-left: 20px;
      margin-top: -3px;
      div {
        max-width: 55vw;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        .font-box-fontStyle {
          font-size: 12px;
          vertical-align: middle;
          color: #4f4d4d;
        }
      }
    }
    .switch-box {
      width: 70px;
      height: 50px;
      position: relative;
      padding-right: 20px;
      span {
        position: absolute;
        top: 16px;
        left: 17px;
        width: 18px;
        height: 18px;
        background-image: url("../../public/img/stop.svg");
        background-size: 100%;
        &.controlIcon {
          background-image: url("../../public/img/start.svg");
        }
      }
    }
  }
  @keyframes picPlaying {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .play-full {
    position: relative;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 2;
    color: white;
    // 背景遮罩层
    img {
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
      filter: blur(25px);
      width: 100vw;
      height: 100vh;
      transform: scale(1.4);
    }
    .mask {
      width: 100vw;
      height: 100vh;
      background: rgba(0, 0, 0, 0.15);
    }
  }
}
</style>