<template>
  <li
    @click="
      $emit('update:music', item);
      $emit('update:playlist');
    "
    class="song-item"
  >
    <div
      class="song-item-detail"
      :class="{
        active: !paused && currentMusic != null && currentMusic.id === item.id,
      }"
    >
      <h5>{{ item.name }}</h5>
      <p>
        <i v-if="item.song.exclusive" class="sq-icon"></i>
        <span v-for="(artist, index) in item.song.artists" :key="index">
          <template v-if="index"> / </template>{{ artist.name }}
        </span>
      </p>
    </div>
    <div class="icon">
      <span
        v-if="currentMusic && currentMusic.id === item.id && !paused"
        class="musicPlay"
      ></span>
      <span v-else class="musicStop"></span>
    </div>
  </li>
</template>

<script>
export default {
  props: ["item", "currentMusic", "paused"],
};
</script>

<style scoped lang="less">
.song-item {
  display: flex;
  padding-left: 10px;
  height: 54.4px;
  position: relative;
  &::after {
    content: "";
    display: block;
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    border: 0 solid rgba(0, 0, 0, 0.1);
    transform-origin: top left;
    border-bottom-width: 1px;
    transform: scale(0.5, 0.5);
    pointer-events: none; //禁止按钮的点击
  }
  .song-item-detail {
    flex: 1;
    padding: 6px 0;
    box-sizing: border-box;
    h5 {
      max-width: 80vw;
      font-size: 16px;
      margin: 3px 0;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    p {
      font-size: 12px;
    }
    &.active {
      color: #d33a31;
    }
    .sq-icon {
      display: inline-block;
      width: 12px;
      height: 8px;
      margin-right: 4px;
      background: url("../../public/img/icon.png") no-repeat;
      background-position: 0 0;
      background-size: 166px 97px;
      vertical-align: middle;
    }
  }
  .icon {
    width: 42px;
    height: 100%;
    padding: 0 10px;
    display: flex;
    align-items: center;
    span {
      display: block;
      width: 22px;
      height: 22px;
    }
    .musicStop {
      background: url("../../public/img/start.png") no-repeat;
      // background-position: -24px 0;
      // background-size: 166px 97px;
      background-size: 100%;
    }
    .musicPlay {
      background: url("../../public/img/stop.png") no-repeat;
      background-size: 100%;
    }
  }
}
</style>