<template>
  <li>
    <i class="clock"></i>
    <span
      @click="
        $emit('update:inputValue', item);
        $emit('search-me');
        $parent.changLocalPosition(index)
      "
      >{{ item }}</span
    >
    <figure>
      <i @click="$parent.removeLocal(index)"></i>
    </figure>
  </li>
</template>

<script>
export default {
  props: ["item", "index"],
};
</script>

<style lang="less">
.local {
  ul {
    li {
      display: flex;
      align-items: center;
      height: 45px;
      i.clock {
        margin: 0 8px;
        width: 13px;
        height: 13px;
        vertical-align: middle;
        background: url("../../public/img/clock.svg") no-repeat;
      }
      span {
        margin-right: 10px;
        flex: 1;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      figure {
        flex: 0 0 auto;
        width: 32px;
        height: 32px;
        line-height: 32px;
        display: block;
        i {
          width: 12px;
          height: 12px;
          background: url("../../public/img/delete2.svg") no-repeat;
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
  }
}
</style>