<template>
  <div
    id="app"
    @touchstart="movingStart"
    @touchmove="movingMove"
    @touchend="movingEnd"
  >
    <!-- 加载时的遮罩层 -->
    <div class="loading" v-show="$parent.isShowLoading">
      <img src="../public/img/wangyiMusic.png" />
    </div>
    <HomeNav v-if="$route.meta.isShowNav" />
    <keep-alive>
      <router-view
        @update:music="currentMusic = $event"
        :currentMusic="currentMusic"
        :paused="paused"
        @update:playlist="
          playlist = $event.songListItem;
          currentIndex = $event.index;
        "
      />
    </keep-alive>

    <Play
      v-if="currentMusic"
      :currentMusic="currentMusic"
      @update:paused="paused = $event"
      :playlist="playlist"
      :currentIndex="currentIndex"
      @update:music="
        currentMusic = $event.item;
        currentIndex = $event.index;
      "
      :currentWords="currentWords"
    />
  </div>
</template>
<script>
import HomeNav from "@/components/HomeNav.vue";
import Play from "@/components/Play.vue";
export default {
  components: {
    HomeNav,
    Play,
  },
  watch: {
    currentMusic(n) {
      this.searchWord(n.id);
    },
  },
  data: function () {
    return {
      currentMusic: null, //当前播放的音乐对象
      paused: null, //暂停的布尔值
      playlist: [], //当前最新音乐的播放列表
      currentIndex: 0, //当前最新音乐正在播放的音乐下标
      currentWords: "",
      movestartX: null,
      moveX: null,
    };
  },
  methods: {
    searchWord(id) {
      // console.log(id);
      this.axios.get("/lyric?id=" + id).then((res) => {
        this.currentWords = res.data.lrc.lyric;
      });
    },
    // 获取开始移动的x坐标
    movingStart(e) {
      // console.log(this.$route);
      // console.log(e.touches[0].clientX);
      this.movestartX = e.touches[0].clientX;
    },
    // 获取移动的x坐标
    movingMove(e) {
      this.moveX = e.touches[0].clientX;
    },
    // 判断触摸点，执行路由跳转
    movingEnd() {
      if (this.movestartX == null || this.moveX == null) {
        return;
      }
      let name = this.$route.name;
      switch (name) {
        case "Recommend":
          if (this.moveX < this.movestartX - 100) {
            this.$router.push({
              path: "/hot",
            });
          }
          break;
        case "Hot":
          if (this.moveX < this.movestartX - 100) {
            this.$router.push({
              path: "/search",
            });
          } else if (this.movestartX < this.moveX - 100) {
            this.$router.push({
              path: "/",
            });
          }
          break;
        case "Search":
          if (this.movestartX < this.moveX - 100) {
            this.$router.push({
              path: "/hot",
            });
          }
          break;
        case "PlayList":
          if (this.movestartX < this.moveX - 100) {
            this.$router.back();
          }
          break;
        case "SingerList":
          if (this.movestartX < this.moveX - 100) {
            this.$router.back();
          }
          break;
      }
      this.clearX();
    },
    // 清除触摸x坐标
    clearX() {
      this.movestartX = null;
      this.moveX = null;
    },
  },
};
</script>
<style scoped lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  max-width: 100vw;
  .loading {
    position: fixed;
    z-index: 99;
    width: 100vw;
    height: 100vh;
    margin: 0;
    background-color: #dd001b;
    img {
      display: block;
      margin: 150px auto;
    }
  }
}
:root {
  --animate-duration: 500ms;
}
</style>
