import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Search from '../views/Search.vue'
import Recommed from '../views/Recommed.vue'
import PlayList from '../views/PlayList.vue'
import SingerList from '../views/SingerList.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Recommend',
    component: Recommed,
    meta: { isShowNav: true },
  },
  {
    path: '/hot',
    name: 'Hot',
    component: () => import(/* webpackChunkName: "about" */ '../views/Hot.vue'),
    meta: { isShowNav: true },

  },
  {
    path: '/search',
    name: 'Search',
    component: Search,
    meta: { isShowNav: true },
  },
  {
    path: '/playList',
    name: 'PlayList',
    component: PlayList,
    meta: { isShowNav: false },
  },
  {
    path: '/singerList',
    name: 'SingerList',
    component: SingerList,
    meta: { isShowNav: false },
  },

]

const router = new VueRouter({
  routes
})

export default router
