<template>
  <div class="recommed">
    <RecommendPlayList :currentRecommends="currentRecommends" 
      >推荐歌单</RecommendPlayList
    >
    <!-- 接收路由传过来的参数，执行update:music,update:playlist -->
    <RecommendNewMusic
      :newSongs="newSongs"
      @update:music="$emit('update:music', $event)"
      :currentMusic="$attrs.currentMusic"
      :paused="$attrs.paused"
      @update:playlist="$emit('update:playlist', $event)"
      >最新音乐</RecommendNewMusic
    >
  </div>
</template>

<script>
import RecommendPlayList from "@/components/RecommendPlayList.vue";
import RecommendNewMusic from "@/components/RecommendNewMusic.vue";
export default {
  name: "Recommed",
  components: {
    RecommendPlayList,
    RecommendNewMusic,
  },
  data: function () {
    return {
      recommends: [],
      newSongs: [],
      randomIndex: null,
      selNum: 6,
      count: 0, //计算已经完成ajax次数
    };
  },
  computed: {
    currentRecommends() {
      // let maxNum = Math.floor(
      //   Math.random() * (this.recommends.length - selNum)
      // );
      return this.recommends.slice(
        this.randomIndex,
        this.randomIndex + this.selNum
      );
    },
  },
  activated() {
    // 在页面刷新的时候重置randomIndex的下标，获取新的图片
    this.randomIndex = Math.floor(
      Math.random() * (this.recommends.length - this.selNum)
    );
    this.randomIndex = this.randomIndex < 0 ? 0 : this.randomIndex;
  },
  created() {
    // this.axios.get("/personalized").then((res) => {
    //   this.recommends = res.data.result;
    // });
    // this.axios.get("/personalized/newsong").then((res) => {
    //   this.newSongs = res.data.result;
    // });
    this.$root.isShowLoading = true;
    this.axios
      .get("/personalized")
      .then((response) => {
        // console.log(response.data.result);
        this.recommends = response.data.result;
      })
      .finally(() => {
        this.count = this.count + 1;
      });

    this.axios
      .get("/personalized/newsong")
      .then((response) => {
        // console.log(response.data.result);
        this.newSongs = response.data.result;
      })
      .finally(() => {
        this.count = this.count + 1;
      });
  },
  watch: {
  // 计算已经到达指定的ajax请求数之后，解开this.$root.isShowLoading 
    count: function (n) {
      if (n >= 2) {
        this.$root.isShowLoading = false;
      }
    },
  },
};
</script>

