<template>
  <div class="header" @click="$emit('close-list')">
    <i class="back" @click="$emit('update-isshowbar')"></i>
    <div class="title">
      <p>
        <span ref="title" :class="{scroll:fontscorll}">{{ currentMusic.name }}</span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["currentMusic"],
  data() {
    return {
      fontscorll:false
    }
  },
  updated: function () {
    this.$nextTick(function () {
      this.fontscorll = this.$refs.title.offsetWidth > this.$refs.title.parentNode.offsetWidth ? true :false;
    });
  },
};
</script>

<style scoped lang="less">
.header {
  margin-bottom: 10px;
  display: flex;
  .back {
    background: url("../../public/img/downArrowhead.png");
    background-position: center;
    background-size: 100%;
    display: block;
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
  .title {
    flex: 1;
    p {
      margin: 0 auto;
      display: block;
      width: 200px;
      height: 40px;
      line-height: 40px;
      overflow: hidden;
      white-space: nowrap;
      // text-overflow: ellipsis;
      // text-align: center;
      position: relative;
      span {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        min-width: 200px;
        height: 100%;
        text-align: center;
        &.scroll {
          animation: fontscroll 10s linear infinite;
        }
      }
    }
  }
  &::after {
    content: "";
    display: block;
    width: 40px;
    height: 40px;
  }
  @keyframes fontscroll {
    0% {
      left: 0;
    }
    20% {
      left: 0;
    }
    50% {
      left: -100px;
    }
    70%{
      left:-100px;
    }
    100% {
      left: 0;
    }
  }
}
</style>