<template>
  <li
    class="searchWordsItem"
    @click="
      $emit('update:inputValue', item);
      $emit('search-me');
    "
  >
    <i></i>
    <span>{{ item }}</span>
  </li>
</template>

<script>
export default {
  props: ["item"],
};
</script>

<style lang="less">
.searchWordsItem {
  width: 100%;
  height: 45px;
  padding-left: 10px;
  position: relative;
  line-height: 45px;
  display: flex;
  align-items: center;
  i {
    display: inline-block;
    margin: 0 8px;
    width: 13px;
    height: 13px;
    background: url("../../public/img/find.svg") no-repeat;
  }
  span {
    font-size: 15px;
    color: #333;
    max-width: 70vw;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .afterLine {
    content: "";
    display: block;
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    border: 0 solid rgba(0, 0, 0, 0.1);
    transform-origin: top left;
    border-bottom-width: 1px;
    transform: scale(0.5, 0.5);
    pointer-events: none; //禁止按钮的点击
  }
  &::after {
    .afterLine;
  }
}
</style>